<template>
  <div>
    <van-nav-bar
            title="正式考试"
            left-arrow
            @click-left="$router.push('/mine')"
    />
    <div style="width: 100%;background: #F5F5F5">
      <div style="font-size: 18px;text-align: left;display: inline-block;padding: 10px">
        <van-tag mark type="primary">单选</van-tag>
        驾驶机动车在道路上违反道路交通安全法的行为
      </div>
    </div>
    <div style="padding: 10px">
      <div class="item_box" @click="chooseOption(item.title)" :key="item.title" v-for="item in optionList" :class="selectOption===item.title?'select_item_box':''">
        <div class="span_text" :class="(item.title===rightOption && selectOption)?'success_color':(item.title!==rightOption && item.title===selectOption)?'wrong_color':''">{{item.title}}</div>
        <span :style="{color: (item.title===rightOption && selectOption)?'#52C41A':(item.title!==rightOption && item.title===selectOption)?'#FA541C':(!selectOption)?'#333':''}">{{item.desc}}</span>
        <van-icon v-if="selectOption && item.title!==rightOption && selectOption===item.title" class="cuo_span" color="#FA541C" name="cross"/>
        <van-icon v-if="selectOption && item.title===rightOption" class="cuo_span" color="#52C41A" name="success"/>
      </div>
    </div>
    <div class="time_box">
      <img style="width: 24px;height: 26px;position:relative;top: 7px;" src="../assets/daojishi@2x.png" alt="">
      剩余时间 44:54
    </div>
    <van-row style="position:absolute;bottom: 0;width: 100%;height: 40px;line-height: 40px;">
      <van-col span="8" style="text-align: center;color: #52C41A;">
        <van-tag round type="success">
          <van-icon name="success" />
        </van-tag>
        正确：10
      </van-col>
      <van-col span="8" style="text-align: center;color: #FA541C;">
        <van-tag round type="warning">
          <van-icon name="cross" />
        </van-tag>
        错误：10
      </van-col>
      <van-col span="8" style="text-align: center;color: #919191;">
        <van-icon size="18" style="position: relative;top: 3px;" name="todo-list-o" />
        <span>题量：52/115</span>
      </van-col>
    </van-row>

  </div>
</template>

<script>
  export default {
    name: "liveExam",
    data(){
      return {
        optionList: [
          {
            title: 'A',
            desc: '违章行为'
          },
          {
            title: 'B',
            desc: '违法行为'
          },
          {
            title: 'C',
            desc: '过失行为'
          },
          {
            title: 'D',
            desc: '违规行为'
          }
        ],
        rightOption: 'A',
        selectOption: '',
      }
    },
    methods:{
      chooseOption(e){
        if(this.selectOption){
          return;
        }
        this.selectOption = e;
      },
    }
  }
</script>

<style scoped>
  .item_box{
    height: 56px;
    line-height:56px;
    margin-bottom: 10px;
    font-size: 18px;
    background: #fff;
    text-align: left;
    padding: 0 15px;
    color: #333333;
  }
  .select_item_box{
    transition: 0.3s;
    box-shadow: 2px 2px 2px #ccc;
  }
  .span_text{
    display: inline-block;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    text-align: center;
    background: rgba(0,87,255,0.1);
    color: #0057FF;
    line-height: 32px;
    margin-right: 5px;
  }
  .success_color{
    background: rgba(82,196,26,0.1) !important;
    color: #52C41A;
  }
  .wrong_color{
    background: rgba(250,84,28,0.1) !important;
    color: #FA541C;
  }

  .cuo_span{
    float: right;
    font-size: 24px;
    font-weight: 600;
    margin-top: 15px;
  }
  .time_box{
    width:180px;
    height:40px;
    line-height: 42px;
    background:rgba(191,191,191,0.2);
    border-radius:27px;
    position: absolute;
    left: 50%;
    margin-left: -90px;
    bottom: 54px;
    box-sizing: border-box;
    padding: 0 17px;
    color: #919191;
    font-size: 16px;
  }
</style>